import React, {Component} from 'react'
import classes from './Quiz.css'
import ActiveQuiz from "../../components/ActiveQuiz/ActiveQuiz";
import FinishedQuiz from "../../components/FinishedQuiz/FinishedQuiz";

class Quiz extends Component {
    state = {
        results: {}, // {[id]: success error
        isFinished: false,
        activeQuestion: 0,
        answerState: null, // {[id]: success error
        quiz: [
            {
                question: 'Куда на курортных пляжах просят не заплывать отдыхающих?',
                rightAnswerId: 2,
                id: 1,
                answers: [
                    {text: 'За горизонт', id: 1},
                    {text: 'За буйки', id: 2},
                    {text: 'За границу', id: 3},
                    {text: 'В камыши', id: 4},
                ]
            },
            {
                question: 'При падении чего принято загадывать желание?',
                rightAnswerId: 3,
                id: 2,
                answers: [
                    {text: 'Температуры', id: 1},
                    {text: 'Дисциплины', id: 2},
                    {text: 'Звезды', id: 3},
                    {text: 'Курса рубля', id: 4},
                ]
            },
            {
                question: 'Как благодарные сограждане окрестили период брежневского правления?',
                rightAnswerId: 2,
                id: 3,
                answers: [
                    {text: 'Простой', id: 1},
                    {text: 'Застой', id: 2},
                    {text: 'Перестой', id: 3},
                    {text: 'Отстой', id: 4},
                ]
            },
            {
                question: 'Что показывает судья футболисту, делая предупреждение?',
                rightAnswerId: 2,
                id: 4,
                answers: [
                    {text: 'Паспорт', id: 1},
                    {text: 'Желтую карточку', id: 2},
                    {text: 'Бюллетень', id: 3},
                    {text: 'Язык', id: 4},
                ]
            },
            {
                question: 'Какое из этих животных чаще всего подвержено бреду?',
                rightAnswerId: 2,
                id: 5,
                answers: [
                    {text: 'Лапчатый гусь', id: 1},
                    {text: 'Сивая кобыла', id: 2},
                    {text: 'Дареный конь', id: 3},
                    {text: 'Злая собака', id: 4},
                ]
            },
        ]
    }
//Функция проверки правильного ответа по клику
    onAnswerClickHandler = (answerId) => {

        const timeout = () => {
            window.setTimeout(() => {
                if (this.isQuizFinished()) {
                    this.setState({
                        isFinished: true
                    });
                } else {
                    //По клику меняеем стейт активного вопроса и используем его номер
                    //При обращении к массиву в компоненте ActiveQuiz
                    //Если меняется ActivQuestion меняется и индекс, и перерисовывается страница
                    this.setState({
                        activeQuestion: this.state.activeQuestion + 1,
                        answerState: null
                    });
                }

                window.clearTimeout(timeout)
            }, 1000)
        }

        if (this.state.answerState) {
            //По [0] элементу потому что мы знаем что у нас присвается только один ключ к правильнмоу ответу
            //И если это присвоенное значение равно проверке, останавливаем функцию return
            //Избавит от бага двойного клика на вопрос
            const key = Object.keys(this.state.answerState)[0]
            if (this.state.answerState[key] === 'succes') {
                return
            }

        }

        const question = this.state.quiz[this.state.activeQuestion]
        const results = this.state.results
        if (question.rightAnswerId === answerId) {
            //Обращаемся в question.id что является id текущего вопроса
            //И в него записываем значение success или error
            if(!results[question.id]){
                //Если в объекте ничего нет то мы не отвечали и можем записать
            results[question.id] = 'succes'
            }
            this.setState({
                answerState: {[answerId]: 'succes'},
                results: results
            });
            timeout()
        } else {
            results[question.id] = 'error'
            this.setState({
                answerState: {[answerId]: 'error'},
                results: results
            });
            timeout()
        }
    }

    isQuizFinished() {
        return this.state.activeQuestion + 1 === this.state.quiz.length
    }

    retryHandler = () => {
        const timeout = window.setTimeout(() => {
            this.setState({
                activeQuestion: 0,
                answerState: null,
                isFinished: false,
                results: {}
            });
            window.clearTimeout(timeout)
        }, 1000)

    }

    render() {
        return (
            <div className={classes.Quiz}>
                <div className={classes.QuizWrapper}>
                    <h1>Ответьте на все вопросы:</h1>
                    {this.state.isFinished
                        ? <FinishedQuiz
                            results={this.state.results}
                            quiz={this.state.quiz }
                            onRetry={this.retryHandler}
                        /> : <ActiveQuiz
                            // Передаем ссылку на элемент массива как параметр
                            //  Который используется в компоненте ActiveQuiz
                            answers={this.state.quiz[this.state.activeQuestion].answers}
                            question={this.state.quiz[this.state.activeQuestion].question}
                            onAnswerClick={this.onAnswerClickHandler}
                            quizLength={this.state.quiz.length}
                            answerNumber={this.state.activeQuestion + 1}
                            state={this.state.answerState}
                        />
                    }
                </div>
            </div>
        )
    }
}

export default Quiz