import React from 'react';
import classes from './ActiveQuiz.css'
import AnswersList from "./AnswersList/AnswersList";

const ActiveQuiz = props => (
    <div className={classes.ActiveQuiz}>
        <p className={classes.Question}>
     <span>
         <strong>{props.answerNumber}.
         &nbsp;{props.question}
         </strong>
     </span>

            <small style={{whiteSpace: 'nowrap'}}>{props.answerNumber} из {props.quizLength}</small>
        </p>
       <AnswersList
       // Забираем параметр из вызова компонента в файле Quiz
       // в котором как параметр задается ссылка на массив ответов
       answers={props.answers}
       onAnswerClick={props.onAnswerClick}
       state={props.state}
       />
    </div>
)

export default ActiveQuiz