import React from 'react'
import classes from './AnswerItem.css'

const AnswerItem = props => {
const cls = [classes.AnswerItem]
    if(props.state) {
        cls.push(classes[props.state])
    }

    return (
        <li className={cls.join(' ')}
            //По клику вызываем функцию которая вызываевает переданную по иерархии
            // функцию проверки на правильность клика в которая принимает id конкретного элемента массива
        onClick={() => props.onAnswerClick(props.answer.id)}
        >
            {/*Отображаем ответ answer который берется из итерируемого массива
            в компоненте AnswerList*/}
            {props.answer.text}
        </li>
    )
}


export default AnswerItem